        if (i < txt1.length) {
          if (i === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt1.charAt(i);
          i++;
          setTimeout(typeWriter, speed = 100);
        } else if (j < txt2.length) {
          if (j === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt2.charAt(j);
          j++;
          setTimeout(typeWriter, speed = 100);
