          if (m === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt5.charAt(m);
          m++;
          setTimeout(typeWriter, speed = 100);
        } else if (n < txt6.length) {
          if (n === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt6.charAt(n);
          n++;
          setTimeout(typeWriter, speed = 100);
        } else if (o < txt7.length) {
          if (o === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt7.charAt(o);
          o++;
          setTimeout(typeWriter, speed = 100);
        } else if (p < txt8.length) {
          if (p === 0) {document.getElementById("headline").innerHTML = '';}
          document.getElementById("headline").innerHTML += txt8.charAt(p);
